export default {
	props: {
		themeColor: {
			type: String,
			default: '#D1372C'
		},
		menuList: {
			type: Array
		},
		independence: {
			type: Boolean,
			default: false
		},
		subKey: {
			type: String
		},
		selectList: {
			type: Object
		}
	},
	data() {
		return {
			itemId: 0
		}
	},

	onShow() {
		// console.log("in show  herer");
	},

	onLoad() {
		// console.log("loading-=====")
	},

	onReady() {
		// console.log(this.menuList);
		this.resetItemId();
	},

	methods: {
		/**
		 * 单击选中
		 * @param {Object} item
		 */
		tapItem(item) {
			this.itemId = item.id;
			this.$emit("confirmHeadItem", item);
		},
		
		/**
		 * 重置选中的id
		 */
		resetItemId(){
			if(Object.keys(this.selectList).length > 0){
				console.log(this.selectList);
				this.itemId = this.selectList.id;
			}
		}
		
	}
}
